import React, { useState, useEffect } from "react";
import "./IngredientsDesktop.scss";
import * as ScrollMagic from "scrollmagic";
import gsap from "gsap";
import HamGarlicIngredients2 from "./images/szynka-pomidory-cebula.png";
import HamGarlicIngredients3 from "./images/czosnek-bazylia.png";

const IngredientsDesktop = () => {
  // eslint-disable-next-line
  const [scrollMagic, setScrollMagic] = useState({
    controller: new ScrollMagic.Controller(),
    timelineOne: gsap.timeline(),
  });
  const { controller, timelineOne } = scrollMagic;
  useEffect(() => {
    // ingredients animation
    timelineOne.staggerFromTo(
      [".hamGarlicIngredients2", ".hamGarlicIngredients3"],
      0.5,
      { y: "50px", opacity: 0 },
      { y: "0", opacity: 1 },
      0.25
    );

    new ScrollMagic.Scene({
      triggerElement: ".ingredients_desktop2",
      triggerHook: 0.7,
    })
      .setTween(timelineOne)
      .addTo(controller);
    // eslint-disable-next-line
  }, []);
  return (
    <div className="ingredients_desktop2">
      <img
        src={HamGarlicIngredients2}
        alt="Szynka pomidory cebula"
        className="hamGarlicIngredients2"
      />
      <img
        src={HamGarlicIngredients3}
        alt="Czosnek bazylia"
        className="hamGarlicIngredients3"
      />
    </div>
  );
};

export default IngredientsDesktop;
